import { useRef, useMemo, useState, useEffect, useCallback, memo } from "react"

import Select from "../../FormFields/Select/Select"
import cn from "classnames"

import Modal from "../../Modal/Modal"

import {dateLocale as locale} from "../../../utils/locale"
import validateForm from "../../../utils/validators/basicFormValidator"
import {decorateContractStatusText as decorateText, formatContractStatusOption as formatOption} from "../../../utils/beautify"

const ContractDetails = props => {
  const form = useRef(null)

  const cbs = useMemo(() => ([]), [])

  const [contract, setContract] = useState(props.contract)

  const [errors, setErrors] = useState({})

  useEffect(() => {
    if (cbs.length) {
      cbs.forEach(cb => cb())
      cbs.length = 0
    }
  }, [cbs, contract])

  const date = new Date(parseInt(props.contract.date)).toLocaleString(undefined, locale)

  const onInputChange = e => {
    setContract({
      ...contract,
      [e.target.name] : e.target.value
    })

    setErrors({
      ...errors,
      [e.target.name] : null
    })
  }

  const onSelectChange = useCallback((name, data) => {
    setContract({
      ...contract,
      [name] : (data || {}).value || ""
    })

    setErrors({
      ...errors,
      [name] : null
    })
  }, [contract])

  const onSave = () => {
    const trimmed = {}

    Object.entries(contract).forEach(([key, value]) => {
      if (typeof value === "string") value = value.trim()
      trimmed[key] = value
    })

    cbs.push(() => {
      const validationErrors = validateForm(form.current)
      if (validationErrors) return setErrors(validationErrors)
      props.onSave(contract)
    })

    setContract(trimmed)
  }

  return (
    <Modal
      isOpen={props.isOpen}
      onRequestClose={props.onRequestClose}
      contentLabel={`Details in contract: ${props.contract.id}`}
      closeModal={props.closeModal}
      className="modal--wide"
      useLoader={props.useLoader}
    >
      <h2 className="modal__heading">Contract details</h2>
      <form className="form modal-form" ref={form} noValidate onSubmit={e => e.preventDefault()}>
        <div className="form__columns">
          <div className="form__col group-wrap">
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
                <label className={cn("form__label")}>
                  <input name="id" disabled value={props.contract.id} className="form__input" />
                  <span className="form__label-text">ID</span>
                </label>
              </div>
            </div>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
                <label className={cn("form__label")}>
                  <input name="date" disabled value={date} className="form__input" />
                  <span className="form__label-text">Date</span>
                </label>
              </div>
            </div>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
                <label className={cn("form__label", { error: !!errors.manager_name })}>
                  <input
                    name="manager_name"
                    value={contract.manager_name}
                    onChange={onInputChange}
                    required
                    minLength={3}
                    className="form__input"
                  />
                  <span className="form__label-text">Manager</span>
                </label>
                <span className="form__alert">{errors.manager_name}</span>
              </div>
            </div>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
                <label className={cn("form__label", { error: !!errors.status })} data-children-required>
                  {useMemo(
                    () => (
                      <Select
                        options={props.statuses}
                        value={contract.status}
                        formatOptionLabel={formatOption}
                        onChange={onSelectChange.bind(null, "status")}
                        name="status"
                        placeholder="Status"
                      />
                    ),
                    [props.statuses, contract, onSelectChange]
                  )}
                  <span className="form__label-text">Status</span>
                </label>
                <span className="form__alert">{errors.status}</span>
              </div>
            </div>
          </div>
          <div className="form__col group-wrap group-wrap--client">
            <span className="group-wrap__title">Client</span>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
              <label className={cn("form__label", { error: !!errors.client_name })}>
                  <input
                    name="client_name"
                    value={contract.client_name}
                    onChange={onInputChange}
                    required
                    minLength={3}
                    className="form__input"
                  />
                  <span className="form__label-text">Name</span>
                </label>
                <span className="form__alert">{errors.client_name}</span>
              </div>
            </div>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
              <label className={cn("form__label", { error: !!errors.email })}>
                  <input
                    data-type="email"
                    name="email"
                    value={contract.email}
                    onChange={onInputChange}
                    required
                    className="form__input"
                  />
                  <span className="form__label-text">Email</span>
                </label>
                <span className="form__alert">{errors.email}</span>
              </div>
            </div>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
              <label className={cn("form__label", { error: !!errors.phone })}>
                  <input
                    data-type="phone"
                    name="phone"
                    value={contract.phone}
                    onChange={onInputChange}
                    required
                    className="form__input"
                  />
                  <span className="form__label-text">Phone</span>
                </label>
                <span className="form__alert">{errors.phone}</span>
              </div>
            </div>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
              <label className={cn("form__label", { error: !!errors.company })}>
                  <input
                    name="company"
                    value={contract.company}
                    onChange={onInputChange}
                    required
                    minLength={3}
                    className="form__input"
                  />
                  <span className="form__label-text">Company</span>
                </label>
                <span className="form__alert">{errors.company}</span>
              </div>
            </div>
          </div>
        </div>
        <div className="form__btn-wrap">
          <button className="form__btn form__btn--submit btn" onClick={onSave}>Save</button>
          <button className="form__btn form__btn--cancel btn" onClick={props.closeModal} type="button">Cancel</button>
        </div>
      </form>
    </Modal>
  )
}

export default memo(ContractDetails)