import { BrowserRouter as Router, Route, Switch, Redirect, useHistory } from "react-router-dom"

import { loginSelector } from "./slices/login"

import Login from "./containers/Login/Login"
import Home from "./containers/Home/Home"
import Contracts from "./containers/Contracts/Contracts"
import { useSelector } from "react-redux"

const PrivateRoute = props => {
  const history = useHistory()
  const { condition, to, ...rest } = props
  const from = history.location.pathname

  //TODO get back correct condition
  return condition ? (
    <Route {...rest} />
  ) : (
    <Redirect
      to={{
        pathname: to,
        state: { from },
      }}
    />
  )
}

const App = () => {
  const { login } = useSelector(loginSelector)

  return (
    <Router>
      <Switch>
        <PrivateRoute
          path="/contracts"
          component={Contracts}
          condition={login.isLoggedIn}
          to="/login"
        />
        <Route path="/login" component={Login} />
        <Route index component={Home} />
      </Switch>
    </Router>
  )
}

export default App
