import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"

import api from "../../api/api"

const initialState = {
  loading: false,
  data: [],
  error: {
    hasError: false,
    details: null,
  },
}

const getStatuses = createAsyncThunk("getStatuses", async (payload, { rejectWithValue, signal }) => {
  const res = await api.fetch("contractsStatuses", signal).catch(err => err)

  if (res.name === "AbortError") throw res

  if (res instanceof Error) return rejectWithValue(res?.details || "Couldn't load data. Please try later")

  return res
})

const slice = createSlice({
  name: "statuses",
  initialState,
  reducers: {
    clearStatusesError: state => {
      state.error = initialState.error
    },
  },
  extraReducers: builder => {
    builder.addCase(getStatuses.pending, state => {
      state.loading = true
    })

    builder.addCase(getStatuses.fulfilled, (state, { payload }) => {
      state.data = payload
      state.loading = false
    })

    builder.addCase(getStatuses.rejected, (state, { payload, error }) => {
      if (error.name === "AbortError") return

      state.error = { details: payload, hasError: true }
      state.loading = false
    })
  },
})

export default slice.reducer

export const contractStatusesSelector = state => state.common.contractStatuses

export const statusesCreators = {
  ...slice.actions
}

export const statusesAsyncs = {
  getStatuses,
}
