export function abortAll(aborts) {
  if (!aborts) return

  if (typeof aborts === "function") aborts()

  Object.entries(aborts).forEach(([key, value]) => {
    if (typeof value === "function") value()
    delete aborts[key]
  })
}

export function makeThunksAbortable(thunks, dispatch, aborts) {
  if (!(thunks && typeof thunks === "object") && typeof thunks !== "function")
    throw new Error("First argument should be a thunk or an object containing thunks")

  if (!aborts || typeof aborts !== "object")
    throw new Error("Third argument must be a plain object, you can initialize it inside mapDispatchToProps function")

  if (typeof thunks === "function") return handleThunk(thunks)

  thunks = { ...thunks }

  Object.entries(thunks).forEach(([key, thunk]) => (thunks[key] = handleThunk(thunk)))

  return thunks

  function handleThunk(thunk) {
    //stop here if "thunk" wasn't created using "createAsyncThunk"
    if (!thunk.typePrefix || !thunk.pending) return params => dispatch(thunk(params))

    let prevId = null

    return function (params) {
      if (typeof aborts[prevId] === "function") aborts[prevId]()
      delete aborts[prevId]

      const promise = dispatch(thunk(params))

      const requestId = promise.requestId

      aborts[requestId] = promise.abort
      prevId = requestId

      promise.finally(() => delete aborts[requestId])

      return promise
    }
  }
}
