import { useEffect, useMemo, useCallback, useState } from "react"
import { connect } from "react-redux"
import { bindActionCreators } from "redux"

import cn from "classnames"

import { decorateContractStatusText as decorateText } from "../../utils/beautify"

import { abortAll, makeThunksAbortable } from "../../utils/cancelling"

import "./Contracts.scss"

import Header from "../Header/Header"
import FilterForm from "../../components/Contracts/FilterForm/FilterForm"
import ReactTable from "../../components/Table/ReactTable"
import Pagination from "../../components/Pagination/Pagination"
import Loader from "../../components/Loader/Loader"
import ContractDetails from "../../components/Contracts/ContractDetails/ContractDetails"
import StatusModal from "../../components/Modal/StatusModal"

import { contractsSelector, contractsCreators, contractsAsyncs } from "../../slices/contracts"
import { contractStatusesSelector, statusesCreators, statusesAsyncs } from "../../slices/common/contractStatuses"

import { ReactComponent as Details } from "../../img/pencil-square.svg"

const columns = ({ onDetailsClick }) => [
  {
    type: "number",
    accessor: "id",
    Header: "ID",
    className: "text-centered",
  },
  {
    type: "date",
    accessor: "date",
    Header: "Date",
  },
  {
    accessor: "client_name",
    Header: "Client name",
  },
  {
    accessor: "manager_name",
    Header: "Manager name",
  },
  {
    accessor: "company",
    Header: "Company",
  },
  {
    accessor: "status",
    Header: "Status",
    className: "text-centered",
    Cell: ({ cell }) => decorateText(cell.value),
  },
  {
    accessor: "details",
    Header: "",
    className: ["text-centered", "no-padding"],
    sort: false,
    Cell: ({ row }) => (
      <button onClick={() => onDetailsClick(row.values.id)} className="table-block__btn table-block__btn--details btn">
        <Details />
      </button>
    ),
  },
]

const HeadBtn = props => {
  return (
    <button className={cn("sort-btn", "btn", props.classNames)} onClick={props.onClick} type="button">
      <span className="sort-btn-text">{props.children}</span>
    </button>
  )
}

const combineMessages = (...messages) => {
  const withType = []
  const structured = {}

  messages.forEach(msg => {
    if (!msg.hasMessage && !msg.hasError) return

    if (msg.hasError) return withType.push({ ...msg, type: "error" })
    if (!msg.type) return withType.push({ ...msg, type: "warning" })

    withType.push({ ...msg })
  })

  withType.forEach(msg => {
    const { type, title, details, clear, ...rest } = msg

    const msgTitle = title || ""
    const msgDetails = []

    if (Array.isArray(details)) {
      msgDetails.push(...details)
    } else if (details) {
      msgDetails.push(details)
    }

    if (!structured[type]) structured[type] = {}

    if (!structured[type][msgTitle]) {
      structured[type][msgTitle] = {
        type,
        title: msgTitle,
        details: [...new Set(msgDetails)],
        callbacks: [clear],
        clear(...args) {
          this.callbacks.forEach(cb => cb.apply(this, args))
        },
        ...rest,
      }
      return
    }

    const allDetails = [...structured[type][msgTitle].details, ...msgDetails]

    structured[type][msgTitle].details = [...new Set(allDetails)]

    structured[type][msgTitle].callbacks.push(clear)
  })

  return Object.values(structured)
    .map(obj => Object.values(obj))
    .flat(Infinity)
}

const Contracts = props => {
  const { contracts, currentContractId, filter, sorting, pagination, message, loading } = props.contracts

  const statuses = props.statuses

  const {
    changeFilter,
    changeSorting,
    changePagination,
    clearSettings,
    clearMessage,
    showContractDetails,
    getContracts,
    updateContract,
    getStatuses,
    clearStatusesError,
    abortAll,
  } = props.actions

  const contract = contracts.find(({ id }) => id === currentContractId)

  const firstMessage = combineMessages(
    { ...message, clear: clearMessage },
    { ...statuses.error, clear: clearStatusesError }
  )[0]

  const options = useMemo(() => statuses.data.map(({ text }) => ({ value: text, label: text })), [statuses])

  const params = useMemo(
    () => ({
      filter,
      sorting,
      pagination,
    }),
    [filter, sorting, pagination]
  )

  useEffect(() => {
    getContracts(null)
    getStatuses()

    return () => abortAll()
  }, [getContracts, getStatuses, abortAll])

  const onClear = useCallback(() => {
    const params = clearSettings()
    getContracts(params)
  }, [clearSettings, getContracts])

  const onFilter = useCallback(
    filter => {
      const params = changeFilter(filter)
      getContracts(params)
    },
    [changeFilter, getContracts]
  )

  const onSort = useCallback(
    sort => {
      const params = changeSorting(sort)
      getContracts(params)
    },
    [changeSorting, getContracts]
  )

  const onPagination = useCallback(
    pagination => {
      const params = changePagination(pagination)
      getContracts(params)
    },
    [changePagination, getContracts]
  )

  const onDetailsClick = useCallback(contractId => showContractDetails(contractId), [showContractDetails])

  const closeDetailsModal = useCallback(() => showContractDetails(null), [showContractDetails])

  const updateCurContract = useCallback(
    contract => {
      updateContract({ ...params, contract })
    },
    [params, updateContract]
  )

  return (
    <>
      <Header />
      <main className="main">
        <div className="container container--align-center">
          <h1 className="heading-1">Contracts</h1>
          <div className={cn("table-block", { "table-block--min-height-400": loading })}>
            <FilterForm filter={filter} statuses={statuses.data} onClear={onClear} onFilter={onFilter} />
            <div className="table-block__scroll-wrap table-block__scroll-wrap--mah-500">
              {contracts.length ? (
                <ReactTable
                  columns={columns({ onDetailsClick })}
                  data={contracts}
                  dateIndex="date"
                  numberIndex="id"
                  idIndex="id"
                  headCellElem={HeadBtn}
                  onSort={onSort}
                  sort={{ ...sorting, ascClassName: "asc", desClassName: "des" }}
                />
              ) : (
                <h2 className="table-block__empty">No data</h2>
              )}
            </div>
            {pagination.total && (
              <Pagination pagination={pagination} onPagination={onPagination} className="table-block__pagination" />
            )}
            {firstMessage && (
              <StatusModal type={firstMessage.type} data={firstMessage} onClose={() => firstMessage.clear()} />
            )}
            {contract && (
              <ContractDetails
                isOpen={!!contract}
                onRequestClose={closeDetailsModal}
                closeModal={closeDetailsModal}
                onSave={updateCurContract}
                contract={contract}
                statuses={options}
                useLoader={loading === "edit"}
              />
            )}
            {loading === true && <Loader />}
          </div>
        </div>
      </main>
    </>
  )
}

export default connect(
  state => ({
    contracts: contractsSelector(state),
    statuses: contractStatusesSelector(state),
  }),
  dispatch => {
    const aborts = {}

    return {
      actions: {
        ...bindActionCreators(contractsCreators, dispatch),
        ...bindActionCreators(statusesCreators, dispatch),
        ...makeThunksAbortable(contractsAsyncs, dispatch, aborts),
        ...makeThunksAbortable(statusesAsyncs, dispatch, aborts),
        abortAll: abortAll.bind(null, aborts),
      },
    }
  }
)(Contracts)
