import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"

import api from "../api/api"

export const initialState = {
  loading: false,
  login: {
    user: "",
    isLoggedIn: false,
    isFound: null,
  },
  error: {
    hasError: false,
    details: "",
  },
}

const loginUser = createAsyncThunk("loginUser", async (params, { rejectWithValue, signal }) => {
  const res = await api.fetch({ resource: "auth", body: params }, signal).catch(err => err)

  if (res.name === "AbortError") throw res

  if (res instanceof Error)
    return rejectWithValue({ error: { details: res.details || "Couldn't login. Please try later" } })

  return res.isFound ? res.user : rejectWithValue({ isFound: false })
})

const loginSlice = createSlice({
  name: "login",
  initialState,
  reducers: {
    logout: state => {
      state.login = initialState.login
      state.loading = false
    },
    clearError: state => {
      state.error = initialState.error
    },
  },
  extraReducers: builder => {
    builder.addCase(loginUser.pending, state => {
      state.loading = true
      state.login.isFound = initialState.login.isFound
    })

    builder.addCase(loginUser.fulfilled, (state, { payload }) => {
      state.login.user = payload
      state.login.isLoggedIn = true
      state.login.isFound = true
      state.loading = false
    })

    builder.addCase(loginUser.rejected, (state, { payload = {}, error }) => {
      if (error.name === "AbortError") return

      state.loading = false

      if (payload.isFound === false) {
        state.login = { ...initialState.login, isFound: false }
      } else if (payload.error) {
        state.login = { ...initialState.login }
        state.error = { details: payload.error.details, hasError: true }
      }
    })
  },
})

const { logout, clearError } = loginSlice.actions

export default loginSlice.reducer

export const loginSelector = state => state.login

export const loginCreators = {
  logout,
  clearError,
}

export const loginAsyncs = {
  loginUser,
}
