import React, { useCallback, useEffect, useRef } from "react"
import { Link } from "react-router-dom"
import { useHistory } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import { CSSTransition } from "react-transition-group"

import NavLinks from "../../components/NavLinks/NavLinks"

import { headerSelector, toggleMenu } from "../../slices/header"
import { loginSelector, loginCreators } from "../../slices/login"

import "./Header.scss"

const { logout } = loginCreators

const links = NavLinks.map(({ path, text }) => (
  <li className="nav__item header__nav-item" key={path}>
    <Link to={path} className="nav__link link header__nav-link">
      {text}
    </Link>
  </li>
))

const Header = () => {
  const menu = useRef(null)

  const dispatch = useDispatch()

  const { sideMenuOpen } = useSelector(headerSelector)
  const { login } = useSelector(loginSelector)

  const history = useHistory()

  const closeMenuOnOutsideClick = useCallback(
    e => menu.current && !menu.current.contains(e.target) && dispatch(toggleMenu(false)),
    [menu]
  )

  const getData = async () => {
    const res = await fetch("/user").then(res => res.json())
    console.log(res)
  }


  //close menu on outside clicks
  useEffect(() => {
    if (sideMenuOpen) setTimeout(() => document.addEventListener("click", closeMenuOnOutsideClick))
    return () => document.removeEventListener("click", closeMenuOnOutsideClick)
  }, [sideMenuOpen, closeMenuOnOutsideClick])

  useEffect(() => {
    dispatch(toggleMenu(false))
    return () => dispatch(toggleMenu(false))
  }, [dispatch])

  const onLoginClick = () => {
    login.isLoggedIn ? dispatch(logout()) : history.push("/login")
  }

  return (
    <header className="header" onClick={getData}>
      <div className="container header__container">
        <button className="header__burger btn" onClick={() => dispatch(toggleMenu(!sideMenuOpen))}>
          <span></span>
          <span></span>
          <span></span>
        </button>
        <CSSTransition nodeRef={menu} in={sideMenuOpen} timeout={150} classNames="header__menu">
          <div ref={menu} className="header__menu">
            <button className="header__close-menu btn" onClick={() => dispatch(toggleMenu(!sideMenuOpen))}>
              <span></span>
              <span></span>
            </button>
            <nav className="header__nav nav">
              <ul className="nav__list header__nav-list">{links}</ul>
            </nav>
          </div>
        </CSSTransition>
        <div className="header__user">
          <span className="header__user-name">{login.isLoggedIn && login.user}</span>
          <button className="header__user-auth btn" onClick={onLoginClick}>
            {login.isLoggedIn ? "Logout" : "Login"}
          </button>
        </div>
      </div>
    </header>
  )
}

export default Header
