const emailRegex =
  /^(([\p{L}\p{Nd}!#$%&'`*+\-_/=?^{|}~.]+)|("[\p{L}\p{Nd}!#$%&'`*+\-_/=?^{|}~. ]+"))@([\p{L}\p{N}]+[\p{L}\p{N}-]*[\p{L}\p{N}]+\.)+\p{L}{2,}$/iu

const validateForm = form => {
  const errors = {}
  let fields = Array.from(form.querySelectorAll("input, select, textarea"))

  fields = fields.filter(field => {
    if (!field.name) return false
    if (field.disabled) return false
    if (!field.parentElement.offsetWidth) return false
    if (field.closest("[data-skip-validation]")) return false
    return true
  })

  fields.forEach(field => {
    const error = validateField(field)
    if (error) errors[field.name] = error
  })

  if (Object.values(errors).every(val => !val)) return null

  return errors
}

const checkPhoneLength = str => {
  str = str.replace(/\D/g, "")
  return str.length >= 6 && str.length <= 14
}

const validateField = field => {
  if (["checkbox", "radio"].includes(field.type) && field.required && !field.checked) return "Required"

  if (!field.value.trim() && (field.required || field.closest("[data-children-required]"))) return "Required"

  let minlength = field.getAttribute("minlength")
  let maxlength = field.getAttribute("maxlength")

  if (minlength) minlength = parseInt(minlength) || 0
  if (maxlength) maxlength = parseInt(maxlength) || 0

  if (minlength && field.value.trim().length < minlength) return `Min length ${minlength}`
  if (maxlength && field.value.trim().length > maxlength) return `Max length ${maxlength}`

  if ((field.type === "email" || field.dataset.type === "email") && !field.value.trim().match(emailRegex))
    return "Incorrect email"

  if ((field.type === "phone" || field.dataset.type === "phone") && !checkPhoneLength(field.value))
    return "Incorrect phone"

  return null
}

export default validateForm
