import { memo } from "react"

import "./Pagination.scss"

import cn from "classnames"

import { ReactComponent as ChevronSingle } from "../../img/chevron-right.svg"
import { ReactComponent as ChevronDouble } from "../../img/chevron-double-right.svg"

const Pagination = props => {
  const pagination = { ...props.pagination }
  const cur = pagination.page
  const total = pagination.total

  const onClick = page => {
    if (page < 1) page = 1
    if (page > total) page = total
    pagination.page = page

    props.onPagination(pagination)
  }

  return (
    <div className={cn("pagination", props.className)}>
      <div className="pagination__body">
        <button
          className="pagination__btn pagination__btn--first"
          onClick={onClick.bind(null, 1)}
          type="button"
          disabled={cur === 1}
        >
          <ChevronDouble />
        </button>
        <button
          className="pagination__btn pagination__btn--prev"
          onClick={onClick.bind(null, cur - 1)}
          type="button"
          disabled={cur === 1}
        >
          <ChevronSingle />
        </button>
        <div className="pagination__pages">
          <span className="pagination__current">{cur}</span>
          <span className="pagination__spacer">/</span>
          <span className="pagination__total">{total}</span>
        </div>
        <button
          className="pagination__btn pagination__btn--next"
          onClick={onClick.bind(null, cur + 1)}
          type="button"
          disabled={cur === total}
        >
          <ChevronSingle />
        </button>
        <button
          className="pagination__btn pagination__btn--last"
          onClick={onClick.bind(null, total)}
          type="button"
          disabled={cur === total}
        >
          <ChevronDouble />
        </button>
      </div>
    </div>
  )
}

export default memo(Pagination)
