import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"

import api from "../api/api"

const initialState = {
  contracts: [],
  loading: false,
  currentContractId: null,
  filter: {
    start_date: 0,
    end_date: 0,
    client_name: "",
    manager_name: "",
    status: "",
  },
  sorting: {
    order: "des",
    active: false,
    col: null,
    type: "",
  },
  pagination: {
    page: 1,
    size: null,
    total: null,
  },
  message: {
    hasMessage: false,
    type: null,
    details: null,
  },
}

const getContracts = createAsyncThunk("getContracts", async (params, { rejectWithValue, signal }) => {
  if (!params) params = createParams(initialState)

  const res = await api.fetch({ resource: "contracts", body: params }, signal).catch(err => err)

  if (res.name === "AbortError") throw res

  if (res instanceof Error) return rejectWithValue(res.details || "Couldn't load data. Please try later")

  return res
})

const updateContract = createAsyncThunk("updateContract", async (params, { rejectWithValue, signal }) => {
  const res = await api.fetch({ resource: "contract", body: params }, signal).catch(err => err)

  if (res.name === "AbortError") throw res

  if (res instanceof Error) return rejectWithValue(res.details || "Couldn't load the data. Please try later")

  return { ...res, message: { details: "Contract successfully updated" } }
})

const slice = createSlice({
  name: "contracts",
  initialState,
  reducers: {
    changeContractsFilter: (state, { payload }) => {
      state.filter = { ...state.filter, ...payload }
    },
    changeContractsSorting: (state, { payload }) => {
      state.sorting = payload
    },
    changeContractsPagination: (state, { payload }) => {
      state.pagination = payload
    },
    clearContractsSettings: state => {
      state.filter = initialState.filter
      state.sorting = initialState.sorting
      state.pagination = initialState.pagination
    },
    clearMessage: state => {
      state.message = initialState.message
    },
    showContractDetails: (state, { payload }) => {
      state.currentContractId = payload
    },
  },
  extraReducers: builder => {
    builder.addCase(getContracts.pending, state => {
      state.loading = true
    })

    builder.addCase(getContracts.fulfilled, (state, { payload }) => {
      state.loading = false
      state.contracts = payload.data
      state.pagination = payload.pagination
    })

    builder.addCase(getContracts.rejected, (state, { payload, error }) => {
      if (error.name === "AbortError") return

      state.loading = false
      state.message = { type: "error", hasMessage: true, details: payload }
    })

    builder.addCase(updateContract.pending, state => {
      state.loading = true
    })

    builder.addCase(updateContract.fulfilled, (state, { payload }) => {
      state.loading = false
      state.contracts = payload.data
      state.pagination = payload.pagination
      state.message = { type: "success", hasMessage: true, details: payload.message.details }
    })

    builder.addCase(updateContract.rejected, (state, { payload, error }) => {
      if (error.name === "AbortError") return

      state.loading = false
      state.message = { type: "error", hasMessage: true, details: payload }
    })
  },
})

export default slice.reducer

export const contractsSelector = state => state.contracts

const changeFilter = filter => (dispatch, getState) => {
  dispatch(slice.actions.changeContractsFilter(filter))

  return createParams(getState().contracts)
}

const changeSorting = sorting => (dispatch, getState) => {
  dispatch(slice.actions.changeContractsSorting(sorting))

  return createParams(getState().contracts)
}

const changePagination = pagination => (dispatch, getState) => {
  dispatch(slice.actions.changeContractsPagination(pagination))

  return createParams(getState().contracts)
}

const clearSettings = () => (dispatch, getState) => {
  dispatch(slice.actions.clearContractsSettings())

  return createParams(getState().contracts)
}

const {clearMessage, showContractDetails} = slice.actions

export const contractsCreators = {
  clearMessage,
  showContractDetails,
  changeFilter,
  changeSorting,
  changePagination,
  clearSettings,
}

export const contractsAsyncs = {
  getContracts,
  updateContract,
}

function createParams(state) {
  return {
    filter: state.filter,
    sorting: state.sorting,
    pagination: state.pagination,
  }
}