import cn from "classnames"

import Modal from "./Modal"
import "./Modal.scss"

import { ReactComponent as Success } from "./check-circle.svg"
import { ReactComponent as Warning } from "./question-circle.svg"
import { ReactComponent as Error } from "./exclamation-circle.svg"

const StatusModal = props => {
  const isOpen = ["error", "warning", "success"].includes(props.type)
  let Icon, title, details

  switch (props.type) {
    case "success":
      Icon = Success
      title = "Success"
      break
    case "warning":
      Icon = Warning
      title = "Warning"
      break
    default:
      Icon = Error
      title = "Error"
      break
  }

  if (props.data.title) title = props.data.title
  if (props.data.details) details = Array.isArray(props.data.details) ? props.data.details : [props.data.details]

  const detailsContent = details.map((item, index) => {
    let text, html

    if (item && typeof item === "object") {
      text = item.text
      html = item.html
    } else {
      text = item
    }

    return (
      <div className="modal__detail-wrap" key={index}>
        <p className="modal__detail-text">{text}</p>
        {html && <div className="modal__detail-mess">{html}</div>}
      </div>
    )
  })

  const closeModal = () => props.onClose()

  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={closeModal}
      closeModal={closeModal}
      contentLabel={`${props.type} modal`}
      className={["modal--info", props.className]}
    >
      <div className={cn("modal__icon", `modal__icon--${props.type}`)}>
        <Icon />
      </div>
      <h2 className="modal__title">{title}</h2>
      {detailsContent}
      <div className="modal__btn-wrap">
        <button className="modal__btn modal__btn--main modal__btn--ok" onClick={closeModal} type="button">
          Ok
        </button>
      </div>
    </Modal>
  )
}

export default StatusModal
