import React from "react"
import { Link } from "react-router-dom"

import Header from "../Header/Header"
import NavLinks from "../../components/NavLinks/NavLinks"

import "./Home.scss"

const cards = NavLinks.filter(({ path }) => path !== "/").map(({ path, text, Icon }) => {
  return (
    <Link to={path} className="nav-cards__card" key={path}>
      <Icon />
      <h2 className="nav-cards__title">{text}</h2>
    </Link>
  )
})

const Home = () => (
  <>
    <Header />
    <main className="main">
      <div className="container">
        <h1 className="visually-hidden">CRM navigation</h1>
        <div className="nav-cards">{cards}</div>
      </div>
    </main>
  </>
)

export default Home
