const REQUIRED = "Required"
const MIN_LENGTH = "Min length 3"
const NO_WHITESPACE = "Whitespace isn't allowed"

const validate = ({name, value}) => {
  const errors = []

  if (name === "user") {
    validateRequired(value, errors)
    validateLength(value, errors)
  } else if (name === "pass") {
    validateRequired(value, errors)
    validateLength(value, errors)
    validateWhitespace(value, errors)
  }

  return errors[0] || ""
}

const validateRequired = (value, errors) => (
  !value.length && errors.push(REQUIRED)
)

const validateLength = (value, errors) => (
  value.length < 3 && errors.push(MIN_LENGTH)
)

const validateWhitespace = (value, errors) => (
  value.includes(" ") && errors.push(NO_WHITESPACE)
)

export default validate
