import { memo, useMemo } from "react"
import { useSortBy, useTable } from "react-table"

import cn from "classnames"

import {dateLocale as locale} from "../../utils/locale"

const getColumnContent = (column, props) => {
  const isSortable = typeof props.onSort === "function" && column.sort !== false

  const { col, active, order, ascClassName, desClassName } = props.sort || {}
  
  if (isSortable) {
    const accessor = column.getHeaderProps().key.split("header_")[1]
    const isNewCol = col !== accessor || !active
    const btnClassName = isNewCol ? "" : order === "des" ? desClassName || "des" : ascClassName || "asc"

    const onClick = () => {
      const sort = { col: accessor, active: true, order: "des", type: column.type || "" }

      if (isNewCol) return props.onSort(sort)

      if (order === "des") {
        sort.order = "asc"
      } else {
        sort.order = "des"
        sort.active = false
      }

      props.onSort(sort)
    }

    return (
      <th {...column.getHeaderProps()} className={cn("sortable-cell", column.className)}>
        <button className={cn("sort-btn", "btn", btnClassName)} onClick={onClick} type="button">
          <span className="sort-btn-text">{column.render("Header")}</span>
        </button>
      </th>
    )
  } else {
    return <th {...column.getHeaderProps()} className={column.className}>{column.render("Header")}</th>
  }
}

const Table = props => {
  const tableInstance = useTable({ columns: props.columns, data: props.data })

  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } = tableInstance

  return (
    <table className="table-block__table" {...getTableProps()}>
      <thead>
        {headerGroups.map(headerGroup => (
          <tr {...headerGroup.getHeaderGroupProps()}>
            {headerGroup.headers.map(column => getColumnContent(column, props))}
          </tr>
        ))}
      </thead>
      <tbody {...getTableBodyProps()}>
        {rows.map(row => {
          prepareRow(row)
          return (
            <tr {...row.getRowProps()}>
              {row.cells.map(cell => {
                let content = cell.render("Cell")

                if (cell.column.type === "date") {
                  content = new Date(parseInt(cell.value)).toLocaleDateString(undefined, locale)
                } else if (typeof cell.column.decorator === "function") {
                  const res = cell.column.decorator(cell.value)
                  if (res != null) content = res
                }

                return (
                  <td {...cell.getCellProps()} className={cn(cell.column.className)}>
                    {content}
                  </td>
                )
              })}
            </tr>
          )
        })}
      </tbody>
    </table>
  )
}

export default memo(Table)
