import ReactModal from "react-modal"
import cn from "classnames"

import "./Modal.scss"

import { ReactComponent as Close } from "./x-lg.svg"

import Loader from "../Loader/Loader"

const Modal = props => {
  ReactModal.setAppElement("#root")

  return (
    <ReactModal
      isOpen={props.isOpen}
      onRequestClose={props.onRequestClose}
      contentLabel={props.contentLabel}
      overlayClassName="modal-overlay"
      className={cn("modal", props.className)}
      bodyOpenClassName="no-overflow"
      htmlOpenClassName="no-overflow"
    >
      <button className="modal__btn modal__btn--close-top" onClick={props.closeModal} type="button">
        <Close />
      </button>
      <div className="modal__container">{props.children}</div>
      {props.useLoader && <Loader />}
    </ReactModal>
  )
}

export default Modal
