export const decorateContractStatusText = text => {
  let className = "text-status"
  const tempText = String(text).toLowerCase()

  switch (tempText) {
    case "draft":
      className += " text-status--draft"
      break
    case "signed":
      className += " text-status--signed"
      break
    case "active":
      className += " text-status--active"
      break
    case "rejected":
      className += " text-status--rejected"
      break
  }

  return <span className={className}>{text}</span>
}

export const formatContractStatusOption = ({ label }) => {
  return decorateContractStatusText(label)
}