import { useMemo, useEffect, useState } from "react"
import { useHistory } from "react-router-dom"
import { bindActionCreators } from "redux"
import { connect } from "react-redux"

import cn from "classnames"

import validateField from "../../utils/validators/loginValidator"
import {makeThunksAbortable, abortAll} from "../../utils/cancelling"

import { loginSelector, initialState, loginCreators, loginAsyncs } from "../../slices/login"

import Header from "../Header/Header"
import Loader from "../../components/Loader/Loader"
import StatusModal from "../../components/Modal/StatusModal"

const validationInitialState = { user: "", pass: "" }

const Login = props => {
  const history = useHistory()
  const from = history?.location?.state?.from

  const cbs = useMemo(() => [], [])

  const { login, loading, error } = props

  const { loginUser, clearError, abortAll } = props.actions

  const [fields, setFields] = useState({ user: login.user, pass: "" })

  const [isFound, setIsFound] = useState(login.isFound)

  const [validation, setValidation] = useState(validationInitialState)

  const { user: userErr, pass: passErr } = validation

  useEffect(() => {
    return () => abortAll()
  }, [abortAll])

  // useEffect(() => {
  //   if (!login.isLoggedIn) return
  //   from ? history.replace(from) : history.replace({ pathname: "/" })
  // }, [login.isLoggedIn, from, history])

  useEffect(() => {
    cbs.forEach(cb => cb(fields))
    cbs.length = 0
  }, [cbs, fields])

  useEffect(() => {
    setFields({ user: login.user || fields.user, pass: "" })
    setIsFound(login.isFound)
  }, [login, fields.user])

  const onInputChange = e => {
    setFields(state => ({ ...state, [e.target.name]: e.target.value }))
    setIsFound(initialState.login.isFound)
    setValidation(validationInitialState)
  }

  const validate = () => {
    const trimmed = {}

    Object.entries(fields).forEach(([name, value]) => (trimmed[name] = value.trim()))

    setFields(trimmed)

    cbs.push(fields => {
      const errors = {}

      Object.entries(fields).forEach(([name, value]) => (errors[name] = validateField({ name, value })))

      if (errors.user || errors.pass) return setValidation(errors)

      loginUser({ user: fields.user, pass: fields.pass })
    })
  }

  return (
    <>
      <Header />
      <main className="main">
        <div className="container container--align-center">
          <h1 className="heading--1">Login</h1>
          <form className="login__form form" onSubmit={e => e.preventDefault()} noValidate>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
                <label className={cn("form__label", { error: userErr })}>
                  <input name="user" value={fields.user} onChange={onInputChange} required className="form__input" />
                  <span className="form__label-text">Name</span>
                </label>
                <span className="form__alert">{userErr}</span>
              </div>
            </div>
            <div className="form__row">
              <div className="form__field-wrap form__field-wrap--grow">
                <label className={cn("form__label", { error: passErr })}>
                  <input
                    type="password"
                    name="pass"
                    value={fields.pass}
                    onChange={onInputChange}
                    required
                    className="form__input"
                  />
                  <span className="form__label-text">Password</span>
                </label>
                <span className="form__alert">{passErr}</span>
              </div>
            </div>
            {isFound === false && (
              <div className="form__row">
                <span className="form__alert-large">The user with such name and/or password not found</span>
              </div>
            )}
            <div className="form__btn-wrap">
              <button className="form__btn form__btn--submit btn" onClick={validate}>
                Sign in
              </button>
            </div>
          </form>
          {loading && <Loader />}
          {error.hasError && <StatusModal type="error" data={error} onClose={() => clearError()} />}
        </div>
      </main>
    </>
  )
}

export default connect(loginSelector, dispatch => {
  const aborts = {}

  return {
    actions: {
      ...bindActionCreators(loginCreators, dispatch),
      ...makeThunksAbortable(loginAsyncs, dispatch, aborts),
      abortAll: abortAll.bind(null, aborts)
    },
  }
})(Login)
