import clone from "clone"

import * as mockData from "./mockData"

const ERROR_MSG_DEFAULT = "Couldn't load the resource"
const ERROR_MSG_UPDATE = "Couldn't save changes"
const ERROR_MSG_AUTH = "Couldn't authenticate"

const ERROR_LOG_NOT_FOUND = "Resource not found"
const ERROR_LOG_NO_PARAMS = "No request parameters"
const ERROR_LOG_WRONG_RESOURCE = "Wrong resource"
const ERROR_LOG_NO_CONTRACT = "Contract isn't specified"
const ERROR_LOG_CONTRACT_NOT_FOUND = "Couldn't find contract"
const ERROR_LOG_AUTH = "User or pass aren't specified"

const ERROR_SIM = "Simulate network/server error"

const URL_CONTRACTS = "aaa"
const URL_CONTRACT = "bbb"
const URL_CONTRACTS_STATUSES = "ccc"
const URL_AUTH = "ddd"

const MOCK_CONTRACTS = true
const MOCK_CONTRACT = true
const MOCK_CONTRACTS_STATUSES = true
const MOCK_AUTH = true

const MOCK_DELAY = 300
const PAGE_SIZE = 8

class ApiError extends Error {
  constructor(arg) {
    super()
    this.name = this.constructor.name

    if (arg && typeof arg === "object") {
      if (arg.message !== undefined) this.message = arg.message

      if (arg.details !== undefined) {
        this.details = arg.details
        //if arg.details is specified and arg.message not, then we duplicate
        //the former to the latter to always see some reasonable message in console
        if (arg.message === undefined) this.message = arg.details
      }
    } else {
      this.message = arg
    }
  }
}

class AbortError extends Error {
  constructor(message) {
    super()
    this.name = this.constructor.name
    this.message = message || "The user aborted a request."
  }
}

async function _fetch(params, signal) {
  return request({params, signal}).catch(handleError)
}

async function request({ params, signal }) {
  if (!params) throw new ApiError(ERROR_LOG_NO_PARAMS)

  params = processRequestParams(params)

  let method = "GET"
  let url
  let mock = false

  switch (params.resource) {
    case "contracts":
      url = URL_CONTRACTS
      method = "POST"
      mock = MOCK_CONTRACTS
      break
    case "contractsStatuses":
      url = URL_CONTRACTS_STATUSES
      mock = MOCK_CONTRACTS_STATUSES
      break
    case "contract":
      if (!params.body.contract) throw new ApiError({ message: ERROR_LOG_NO_CONTRACT, details: ERROR_MSG_UPDATE })
      url = URL_CONTRACT
      method = "PUT"
      mock = MOCK_CONTRACT
      break
    case "auth":
      if (!params.body.user || !params.body.pass)
        throw new ApiError({ message: ERROR_LOG_AUTH, details: ERROR_MSG_AUTH })
      url = URL_AUTH
      method = "POST"
      mock = MOCK_AUTH
      break
  }

  if (!url) throw new ApiError(ERROR_LOG_WRONG_RESOURCE)

  return mock
    ? mockRequest({ resource: params.resource, body: params.body, signal })
    : makeRequest({ url, method, body: params.body, signal })
}

function makeRequest({ url, method, body, signal }) {
  const options = { method, signal: signal }

  if (method !== "GET") options.body = JSON.stringify(body)

  return fetch(url, options)
    .then(res => {
      if (!res.ok) throw new ApiError(ERROR_LOG_NOT_FOUND)
      return res
    })
    .then(res => res.json())
}

async function mockRequest({ resource, body, signal }) {
  if (Math.random() < 0.05) throw new ApiError({ details: ERROR_SIM })

  body = clone(body)
  let data

  switch (resource) {
    case "contracts":
    case "contract":
      data = mockData.contracts
      if (resource === "contract") updateContract(data, body)
      data = filterContracts(data, body)
      data = sortContracts(data, body)
      data = createPagination(data, body)
      break
    case "contractsStatuses":
      data = mockData.contractsStatuses
      break
    case "auth":
      data = findUser(mockData.users, body)
      break
  }

  await delay({ ms: MOCK_DELAY, signal })

  return clone(data)
}

function filterContracts(contracts, params) {
  if (!params.filter) return contracts

  const { start_date, end_date, client_name: clName, manager_name: mnName, status: statusParam } = params.filter

  return contracts.filter(({ date, client_name, manager_name, status }) => {
    const dateMatches = date >= start_date && (!end_date || date <= end_date)
    const clientMatches = clName.length < 3 || client_name.toLowerCase().includes(clName.toLowerCase())
    const managerMatches = mnName.length < 3 || manager_name.toLowerCase().includes(mnName.toLowerCase())
    const statusMatches = !statusParam || status.toLowerCase() === statusParam.toLowerCase()

    return dateMatches && clientMatches && managerMatches && statusMatches
  })
}

function sortContracts(contracts, params) {
  if (!params.sorting) return contracts

  const { order, active, col, type } = params.sorting

  if (!active) return contracts

  return contracts.sort((itemCur, itemNext) => {
    const valCur = itemCur[col]
    const valNext = itemNext[col]

    if (valCur == null || valNext == null) return 0

    const sign = order === "des" ? 1 : -1

    if (type === "number") return sign * (valCur - valNext)

    if (type === "date") return sign * (new Date(parseInt(valCur)) - new Date(parseInt(valNext)))

    return sign * valCur.localeCompare(valNext)
  })
}

function createPagination(data, { pagination }) {
  if (!pagination || typeof pagination !== "object") {
    pagination = { page: 1 }
  } else if (!pagination.page) {
    pagination.page = 1
  }

  pagination.size = PAGE_SIZE

  let total = Math.ceil(data.length / pagination.size)
  if (total < 1) total = 1
  pagination.total = total

  const { page, size } = pagination

  data = data.slice((page - 1) * size, page * size)

  return { data, pagination }
}

function handleError(err) {
  // console.log(err)
  throw (err.details !== undefined && err instanceof ApiError) || err instanceof AbortError
    ? err
    : new ApiError({ details: ERROR_MSG_DEFAULT, message: err.message })
}

function processRequestParams(params) {
  if (params && typeof params === "object") {
    params = { ...params }
  } else {
    params = { resource: params }
  }

  if (!params.body || typeof params.body !== "object") params.body = {}

  return params
}

function delay({ data, ms = 500, signal }) {
  return new Promise((resolve, reject) => {
    const timeoutId = setTimeout(() => resolve(data), ms)

    signal.addEventListener('abort', () => {
      clearTimeout(timeoutId)
      reject(new AbortError())
    })
  })
}

function findUser(users, target) {
  const result = users.find(({ user, pass }) => {
    return user.toLowerCase() === target.user.toLowerCase() && pass === target.pass
  })

  return result ? { user: result.user, isFound: true, isLoggedIn: true } : { isFound: false }
}

function updateContract(contracts, { contract }) {
  const match = contracts.find(({ id }) => id === contract.id)

  if (!match) throw new ApiError(ERROR_LOG_CONTRACT_NOT_FOUND)

  Object.assign(match, contract)
}

const api = {
  fetch: _fetch,
}

export default api