import ReactSelect from 'react-select'

const styles = {
  container: (provided) => ({
    ...provided,
    fontSize: "1.6rem",
    flexGrow: 1
  }),
  control: (provided, state) => ({
    ...provided,
    backgroundColor: "var(--bg, #fff)",
    cursor: "pointer",
    borderColor: state.isFocused ? "var(--accent)" : "var(--bd-color, #333)",
    boxShadow: "none",
    "&:hover": {},
  }),
  singleValue: (provided) => ({
    ...provided,
    color: "inherit"
  }),
  menu: (provided) => ({
    ...provided,
    backgroundColor: "var(--bg, #fff)",
  }),
}

const Select = props => {
  let val = props.value

  if (!val) {
    val = null
  } else if (typeof val === "string") {
    val = {value: val, label: val}
  }
  
  return (
    <ReactSelect {...props} isClearable styles={styles} value={val} />
  )
}

export default Select