import { useState, useCallback, useMemo, memo, useEffect } from "react"

import { formatContractStatusOption as formatOption } from "../../../utils/beautify"

import DateInput from "../../FormFields/DateInput/DateInput"
import Select from "../../FormFields/Select/Select"

import { ReactComponent as Reset } from "../../../img/reset.svg"

const FilterForm = props => {
  const [filter, setFilter] = useState(props.filter)

  const options = useMemo(() => props.statuses.map(({ text }) => ({ value: text, label: text })), [props.statuses])

  useEffect(() => setFilter(props.filter), [props.filter])

  const onDateChange = useCallback(
    (name, stamp) => setFilter({ ...filter, ...{ [name]: +stamp } }),
    [filter, setFilter]
  )

  const onInputChange = e => setFilter({ ...filter, ...{ [e.target.name]: e.target.value.trim() } })

  const onSelectChange = useCallback(
    (option, state) => setFilter({ ...filter, ...{ [state.name]: (option || {}).value || "" } }),
    [filter, setFilter]
  )

  return (
    <form className="table-block__form form form--single-row" onSubmit={e => e.preventDefault()}>
      <div className="form__row form__row--md-col">
        <div className="form__fields-wrap form__fields-wrap--md-wrap form__fields-wrap--maw-700">
          <div className="form__field-wrap form__field-wrap--grow form__field-wrap--input-narrow">
            <label className="form__label">
              {useMemo(
                () => (
                  <DateInput
                    dateFormat="dd.MM.yyyy"
                    selected={filter.start_date}
                    onChange={onDateChange.bind(null, "start_date")}
                    selectsStart
                    startDate={filter.start_date}
                    endDate={filter.end_date}
                    name="start_date"
                    className="form__input"
                  />
                ),
                [filter.start_date, filter.end_date, onDateChange]
              )}
              <span className="form__label-text">Start Date</span>
            </label>
          </div>
          <div className="form__field-wrap form__field-wrap--grow form__field-wrap--input-narrow">
            <label className="form__label">
              {useMemo(
                () => (
                  <DateInput
                    dateFormat="dd.MM.yyyy"
                    selected={filter.end_date}
                    onChange={onDateChange.bind(null, "end_date")}
                    selectsEnd
                    startDate={filter.start_date}
                    endDate={filter.end_date}
                    minDate={filter.start_date}
                    name="end_date"
                    className="form__input"
                  />
                ),
                [filter.start_date, filter.end_date, onDateChange]
              )}
              <span className="form__label-text">End Date</span>
            </label>
          </div>
          <div className="form__field-wrap form__field-wrap--grow form__field-wrap--input-narrow">
            <label className="form__label">
              <input value={filter.client_name} onChange={onInputChange} name="client_name" className="form__input" />
              <span className="form__label-text">Client</span>
            </label>
          </div>
          <div className="form__field-wrap form__field-wrap--grow form__field-wrap--input-narrow">
            <label className="form__label">
              <input value={filter.manager_name} onChange={onInputChange} name="manager_name" className="form__input" />
              <span className="form__label-text">Manager</span>
            </label>
          </div>
          <div className="form__field-wrap form__field-wrap--select">
            <label className="form__label">
              {useMemo(
                () => (
                  <Select
                    options={options}
                    value={filter.status}
                    formatOptionLabel={formatOption}
                    onChange={onSelectChange}
                    name="status"
                    placeholder=""
                  />
                ),
                [options, filter.status, onSelectChange]
              )}
              <span className="form__label-text">Status</span>
            </label>
          </div>
        </div>
        <button className="form__btn form__btn--submit btn" onClick={() => props.onFilter(filter)}>
          Apply
        </button>
      </div>
      <button className="form__btn-reset btn" type="button" onClick={props.onClear}>
        <Reset />
        Reset
      </button>
    </form>
  )
}

export default memo(FilterForm)
